#contact-form{
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto;
    /* grid-template-rows: auto repeat(4, 3rem) repeat(2, 4rem) repeat(17, 3rem) auto 3rem 3rem; */
    grid-template-areas: 
        "title title"
        "platform platform-input"
        "name name-input"
        "phone phone-input"
        "tel tel-input"
        "address address-input"
        "id-address id-address-input"
        "email email-input"
        "hr1 hr1"
        "com-name com-name-input"
        "com-tel com-tel-input"
        "com-address com-address-input"
        "hr2 hr2"
        "emerg-name emerg-name-input"
        "emerg-tel emerg-tel-input"
        "emerg-address emerg-address-input"
        "hr3 hr3"
        "file file-input"
        "file file-input"
        "file file-input"
        "file file-input"
        "hr4 hr4"
        "contact-time contact-time"
        "hr5 hr5"
        "message message-textarea"
        "hr6 hr6"
        "... rule-read"
        "... reCAP"
        "submit submit";
    gap: 1.5rem 2rem;
    margin: 2rem auto;
    padding: 2rem 5rem;
    box-shadow: 0px 0px 4px var(--glow-color);
    border-radius: 0.5rem;
    max-width: 1024px;
    background-color: var(--glass-color);
    backdrop-filter: blur(5px);

    label{
        margin-top: auto;
        margin-bottom: auto;
        font-size: 1.2rem;

        span{
            font-size: 0.9rem;
        }
    }
    #title{
        grid-area: title;
        text-align: center;
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
        h1{
            font-size: 1.8rem;
            font-weight: 600;
            margin-bottom: 0.5rem;
        }
        p{
            margin: 0rem;
        }
    }
    .address{
        display: grid;
        grid-template-columns: 1fr auto;

        fieldset{
            margin: 0rem 1rem;
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-template-rows: auto 1fr;
            justify-content: center;
            align-items: center;
            gap: 0.2rem 0.5rem;

            input{
                grid-row: 2/3;
                width: 100%;
                height: 100%;
                padding: 0rem;
                margin: auto;
            }
            label{
                margin: 0rem;
                text-align: center;
            }
        }
    }
    .g-recaptcha{
        /* grid-area: reCAP; */
        grid-column: 2/3;
    }
    #rule-read-field{
        /* grid-area: rule-read; */
        grid-column: 2/3;
    }

    #submit{
        /* grid-area: submit; */
        grid-column: 1/3;
        max-width: 10rem;
        margin: 2rem auto;
        background-color: var(--theme-glass-color);
        box-shadow: 0px 0px 4px 2px var(--theme-color);
        border: 1px solid transparent;
    }
    #submit:hover{
        background-color: var(--theme-glass-color);
        border: 1px solid var(--text);
        box-shadow: 0px 0px 10px 5px var(--theme-color);
    }
    hr{
        grid-column: 1/3;
        height: 0px;
        width: 100%;
        border: 1px solid var(--text);
        opacity: 0.1;
        margin: auto;
    }
    fieldset{
        border: none;
        height: 100%;
        display: flex;
        align-items: center;

        label{
            margin-right: 2rem;
            cursor: pointer;
        }
    }
    a, #rule-link{
        color: var(--theme-dim-color);
        text-shadow: 0px 0px 20px var(--theme-color);
        margin: auto 0.5rem;
        font-weight: 600;
        font-size: 1.2rem;
        text-decoration: underline;
        text-underline-offset: 4px;
    }
}