html{
  font-size: 14px;
  font-weight: 500;
  /* scrollbar-color: #c2c2c2 #fff;
  scrollbar-width: thin; */
}
@media (max-width: 992px) {
  html{
    font-size: 12px;
  }
}
@media (max-width: 768px) {
  html{
    font-size: 10px;
  }
}
@media (max-width: 576px) {
  html{
    font-size: 8px;
  }
}

::-webkit-scrollbar{
  width: 10px;
}

::-webkit-scrollbar-track{
  background: #fff;
  width: thin;
}


::-webkit-scrollbar-thumb {
  background: #c2c2c2; /* 滑块颜色 */
  border-radius: 10px; /* 圆角 */
  border: 2px solid #fff;
}

:root{
  --theme-color: #268dcb;
  --theme-dim-color: #a9d4ec;
  --theme-glass-color: #268dcb50;
  --background-color: #06080E;
  --background-gradient: linear-gradient(150deg, #06080E, #17283C, #06080E, #122133);
  --title: #ffffff;
  --text: #f5f5f5;
  --border-color: #c5c5c5;
  --glass-color: #ffffff10;
  --shadow-color: #00000050;
  --glow-color: #ffffff;
  --glow-dim-color: #ffffff50;
  --dark-glass-color: #00000030;
}

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  font-family: "Outfit", sans-serif;
  height: max-content;
  min-height: 100dvh;
}

#root{
  box-sizing: content-box;
  min-height: 100dvh;
  position: relative;
}

body{
  box-sizing: content-box;
  background-color: var(--background-color);
  color: var(--title);
  /* height: 100dvh; */
  width: 100dvw;
  background-image: var(--background-gradient);
  background-attachment: fixed;
  background-size: 400%;
  animation: bg-anim 10s ease-in-out infinite alternate;
  overflow-x: hidden;
}


@keyframes bg-anim{
  0%  {background-position: left;}
  100%  {background-position: right;}
}

h1{
  font-size: 5rem;
  font-weight: 400;
}

h2{
  font-size: 3rem;
  letter-spacing: 0.2rem;
  font-weight: 700;
}

h3{
  font-size: 2rem;
  letter-spacing: 0.3rem;
  font-weight: 400;
}

h4{
  font-size: 1.8rem;
  font-weight: 300;
  letter-spacing: 0.2rem;
}

p{
  letter-spacing: 0.1rem;
  line-height: 200%;
  margin-top: 1rem;
}

img{
  width: 100%;
}
input, textarea{
  background-color: #e8f0fe;
  border-style: none;
  box-shadow: 0px 0px 4px var(--glow-color);
  border-radius: 0.5rem;
  color: var(--text);
  font-size: 1.2rem;
  padding: 1rem 2rem;
  min-height: 2.5rem;
  transition: all 0.3s;
  opacity: 0.8;
}
textarea{
  font-style: italic;
  color: var(--background-color);
  padding-top: 1rem;
  width: 100% !important;
  min-height: 10rem;
}
input::placeholder{
  color: var(--background-color);
  opacity: 0.5;
}
/* input:invalid{
  box-shadow: 0px 0px 10px #ff0000;
  background-color: #ffcece;
  border: 2px solid var(--glow-color);
  color: #ff3939;
} */
input:focus,
textarea:focus{
  opacity: 1;
}
input,
input:focus,
textarea:focus,
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:valid{
  color: var(--background-color);
  background-color: #e8f0fe;
  -webkit-box-shadow: 0 0 0px 1000px #000 inset;
  border: 1px solid var(--text);
  box-shadow: 0px 0px 10px var(--theme-color) !important;
  appearance: none;
}

input[type="checkbox"], input[type="checkbox"][required], input[type="radio"]{
  background-color: transparent !important;
  display: block;
  box-shadow: 0px 0px 2px 1px var(--glass-color) !important;
  padding: 0rem;
  margin-right: 1rem;
  height: 80%;
  aspect-ratio: 1/1;  
  cursor: pointer;

  background-image: none;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
input[type="checkbox"]:checked, input[type="radio"]:checked{
  background-image: url(/public/pictures/check_24dp_FILL0_wght400_GRAD0_opsz24.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  box-shadow: 0px 0px 4px 2px var(--theme-color) !important;
}
input[type="file"]{
  position: relative;
  background-color: transparent;
  box-shadow: none !important;
  border: none !important;
  margin: auto 0rem;
  padding: 0rem;
  color: var(--border-color);
  cursor: pointer;
}
input[type="file"]:valid,
input[type="file"]:focus{
  border: none !important;
  box-shadow: none !important;
  color: var(--text);
  text-shadow: 0px 0px 10px var(--theme-color);
}
input::file-selector-button{
  background-color: var(--glass-color);
  border-style: none;
  color: transparent;
  box-shadow: 0px 0px 4px 2px var(--glow-color);
  padding: 0.5rem 2rem;
  margin: 4px 1rem 4px 4px;
  border-radius: 10rem;
}
input[type="file"]::before{
  content: "選擇圖片";
  position: absolute;
  top: 50%;
  left: 2.5rem;
  font-style: normal;
  transform: translate(0%, -50%);
}

input:disabled{
  opacity: 0.2;
}

button{
  padding: 1rem 2rem;
  border-radius: 10rem;
  background-color: transparent;
  border-style: none;
  color: var(--text);
  box-shadow: 0px 0px 4px var(--shadow-color);
  backdrop-filter: blur(10px);
  font-size: 1.2rem;
  letter-spacing: 0.2rem;
  cursor: pointer;

  transition: all 0.5s;
}
button:hover{
  background-color: var(--glass-color);
  box-shadow: 0px 0px 4px var(--glow-color);
}

legend{
  font-size: 1.8rem;
  padding: 0rem 2rem;
}

dialog{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-width: 992px;
  height: 80%;
  overflow: hidden;
  overflow-y: scroll;
  display: none;
  gap: 1rem;
  background-color: var(--glass-color);
  backdrop-filter: blur(10px);
  border-style: none;
  border: 1px solid var(--text);
  border-radius: 12px;
  padding: 2rem;
  z-index: 150;

  scrollbar-color: #c2c2c2 #fff !important;
  scrollbar-width: 10px;
}
dialog[open]{
  display: grid;
}
dialog::backdrop {
  background-color: var(--glass-color);
  backdrop-filter: blur(10px);
  z-index: 149;
}
.close-btn{
  background-color: var(--theme-color);
  display: flex;
  position: sticky;
  top: 0rem;
  margin-left: auto;
  max-width: 3rem;
  width: 10%;
  aspect-ratio: 1/1;
  padding: 0rem;
  img{
    width: 80%;
    aspect-ratio: 1/1;
    margin: auto;
  }
}

.pointer{
  cursor: pointer;
}

.link-wrapper{
  position: relative;
}
.link-sensor{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99;
}

.no-style{
  list-style-type: none;
}

.reading-sec{
  max-width: 1024px;
  width: calc(100% - 2rem);
  margin: auto;
  padding: 1rem
}

.card{
  box-shadow: 0px 0px 4px var(--glow-color);
  background-color: var(--glass-color);
  padding: 1.5rem;
  border-radius: 1rem;
  backdrop-filter: blur(5px);
}

.text-center{
  text-align: center;
}

.text-glow{
  text-shadow: 0px 0px 10px var(--theme-color);
}